  font-size: inherit; }

.artcontent h3 tt,.artcontent  h3 code {
  font-size: inherit; }

.artcontent h4 tt,.artcontent  h4 code {
  font-size: inherit; }

.artcontent h5 tt,.artcontent  h5 code {
  font-size: inherit; }

.artcontent h6 tt, .artcontent h6 code {
