    margin-top: 0;
    padding-top: 0; }
.artcontent  > h3:first-child, .artcontent  > h4:first-child, .artcontent  > h5:first-child, .artcontent  > h6:first-child {
  margin-top: 0;
  padding-top: 0; }

.artcontent a:first-child h1,.artcontent  a:first-child h2,.artcontent  a:first-child h3,.artcontent  a:first-child h4, .artcontent a:first-child h5, .artcontent a:first-child h6 {
