  padding-top: 0; }

.artcontent h1 p, .artcontent h2 p,.artcontent  h3 p, .artcontent h4 p,.artcontent  h5 p,.artcontent  h6 p {
  margin-top: 0; }

.artcontent li p.first {
  display: inline-block; }
.artcontent li {
  margin: 0; }
.artcontent ul, .artcontent ol {
  padding-left: 30px; }

.artcontent ul :first-child, ol :first-child {
  margin-top: 0; }

.artcontent dl {
  padding: 0; }
 .artcontent  dl dt {
    font-size: 14px;
    font-weight: bold;
    font-style: italic;
    padding: 0;
    margin: 15px 0 5px; }
  .artcontent   dl dt:first-child {
      padding: 0; }
   .artcontent  dl dt > :first-child {
      margin-top: 0; }
  .artcontent   dl dt > :last-child {
      margin-bottom: 0; }
  .artcontent dl dd {
    margin: 0 0 15px;
    padding: 0 15px; }
  .artcontent   dl dd > :first-child {
      margin-top: 0; }
   .artcontent  dl dd > :last-child {
      margin-bottom: 0; }

.artcontent blockquote {
  border-left: 4px solid #2B3F52;
  /*padding: 0 15px;*/
  padding: 15px;
  color: #2B3F52; }
 .artcontent  blockquote > :first-child {
    margin-top: 0; }
 .artcontent  blockquote > :last-child {
    margin-bottom: 0; }

.artcontent table {
  padding: 0;border-collapse: collapse; }
  .artcontent table tr {
    border-top: 1px solid #cccccc;
    background-color: white;
    margin: 0;
    padding: 0; }
    .artcontent table tr:nth-child(2n) {
      background-color: #f8f8f8; }
  .artcontent   table tr th {
      font-weight: bold;
      border: 1px solid #cccccc;
