.artcontent img {
  max-width: 100%; }

.artcontent span.frame {
  display: block;
  overflow: hidden; }
 .artcontent  span.frame > span {
    border: 1px solid #dddddd;
    display: block;
    float: left;
    overflow: hidden;
    margin: 13px 0 0;
    padding: 7px;
    width: auto; }
 .artcontent  span.frame span img {
    display: block;
    float: left; }
 .artcontent  span.frame span span {
    clear: both;
    color: #333333;
    display: block;
    padding: 5px 0 0; }
.artcontent span.align-center {
  display: block;
  overflow: hidden;
  clear: both; }
  span.align-center > span {
    display: block;
    overflow: hidden;
    margin: 13px auto 0;
    text-align: center; }
  span.align-center span img {
    margin: 0 auto;
    text-align: center; }
.artcontent span.align-right {
  display: block;
  overflow: hidden;
  clear: both; }
 .artcontent  span.align-right > span {
    display: block;
    overflow: hidden;
    margin: 13px 0 0;
    text-align: right; }
  .artcontent span.align-right span img {
    margin: 0;
    text-align: right; }
.artcontent span.float-left {
  display: block;
  margin-right: 13px;
  overflow: hidden;
  float: left; }
  .artcontent span.float-left span {
    margin: 13px 0 0; }
.artcontent span.float-right {
  display: block;
  margin-left: 13px;
  overflow: hidden;
  float: right; }
  .artcontent span.float-right > span {
