.artcontent code,.artcontent tt {
  margin: 0 2px;
  padding: 0 5px;
  white-space: nowrap;
  border: 1px solid #9c978f;
  background-color: #dedede;
  border-radius: 3px; }

.artcontent pre code {
  margin: 0;
  padding: 0;
  /*white-space: pre;*/
  border: none;
  background: transparent; }
.artcontent ol{
  list-style: decimal;
}
.artcontent .highlight pre {
  background-color: #f8f8f8;
  border: 1px solid #cccccc;
