  line-height: 19px;
  overflow: auto;
  padding: 6px 10px;
  border-radius: 3px; }
 .artcontent  pre code, pre tt {
    background-color: transparent;
    border: none; }

.artcontent sup {
    font-size: 0.83em;
    vertical-align: super;
    line-height: 0;
}

.artcontent code {
    white-space: pre-wrap;
    word-break: break-all;
    /*display: block;*/
}


